class Preview extends Component {
    render() {
      const {
        match: {
          params: { previewId },
          url
        }
      } = this.props;
      return (
        <div>
          <h4>Preview: {previewId}</h4>
  
          <Link to={`${url}/review/1`}>review 1</Link>
          <Link to={`${url}/review/2`}>review 2</Link>
  
          <Route path={`${url}/review/:reviewId`} component={ReviewWithRouter} />
        </div>
      );
    }
  }
  
  class Review extends Component {
    render() {
      const {
        match: {
          params: { reviewId }
        }
      } = this.props;
      return (
        <div>
          <h4>Review {reviewId} with router</h4>
        </div>
      );
    }
  }
  
  const ReviewWithRouter = withRouter(Review);
  
  function App() {
    return (
      <div className="App">
        <Router>
          <>
            <div>
              <Link to="/preview/1">preview 1</Link>
              <Link to="/preview/2">preview 2</Link>
            </div>
            <Route path="/preview/:previewId" component={Preview} />
          </>
        </Router>
      </div>
    );
  }
  
  const rootElement = document.getElementById("root");
  ReactDOM.render(<App />, rootElement);